.App {
  min-height: 100vh;
  background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
}

.search-form {
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-button {
  border: none;
  background-color: none;
}


.recipe {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}